.kontakty_content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 3.75rem;
  margin-bottom: 3.5rem;
}
.kontakty_content_elem {
  text-align: center;
  padding: 1rem 0;
  background: linear-gradient(270.63deg, #7B25CD 0%, #A74EE5 100%);
  font-weight: bold;
}

.galereya {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}
.galereya_elem {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}
.galereya_elem a {
  width: 100%;
}
.galereya_elem a img {
  width: 100%;
  object-fit: fill;
}

.zakupki table tr:first-child td {
  background: #7B25CD;
  border: 1px solid #fff;
}
.zakupki table tr td:first-child {
  width: 25%;
}
.zakupki table tr td:nth-child(2) {
  width: 65%;
}
.zakupki table tr td:last-child {
  text-align: center;
}
.zakupki table tr td {
  padding: 1rem;
  border: 1px solid #fff;
  line-height: 1.45rem;
  vertical-align: middle;
}

@media screen and (max-width: 1500px) {
  html {
    font-size: 14px;
  }
  .wrapper, .lines {
    width: 1170px;
  }
  .main .-img .clouds {
    left: 193px;
  }
  @keyframes clouds {
    0% {
      top: 25px;
      left: 200px;
    }
    30% {
      opacity: 1;
    }
    100%, 50% {
      top: -210px;
      left: 391px;
      opacity: 0;
    }
  }
  .license {
    gap: 5rem;
  }
  .license_content {
    padding: 2.5rem;
  }
  .company_info {
    max-width: 605px;
  }
  .company_info .blocks {
    margin-top: 3.5rem;
    gap: 2.3rem;
    width: 500px;
  }
  .company_info .-desc {
    max-width: 476px;
  }
  .company_info .-desc i {
    line-height: 156%;
    margin-bottom: 1rem;
  }
  .map_content {
    right: calc((100% - 1170px) / 2);
  }
}
@media screen and (max-width: 1199px) {
  .wrapper, .lines {
    width: 960px;
  }
  .main .-text .h1 {
    font-size: 2.8rem;
  }
  .main .-text .-sub_title {
    font-size: 1.15rem;
  }
  .main .-img {
    width: 52%;
  }
  .main .-img .clouds {
    width: 130px;
    left: 180px;
  }
  @keyframes clouds {
    0% {
      top: 25px;
      left: 180px;
    }
    30% {
      opacity: 1;
    }
    100%, 50% {
      top: -180px;
      left: 341px;
      opacity: 0;
    }
  }
  .license {
    gap: 3rem;
  }
  .license_nav {
    width: auto;
  }
  .license_nav_wrapper {
    flex-shrink: 0;
    width: 45%;
  }
  .license_nav_elem {
    padding: 1.5rem;
  }
  .license_content {
    padding: 2rem;
    max-height: 473px;
  }
  .company {
    position: relative;
  }
  .company_img {
    position: absolute;
    width: 54%;
    z-index: -1;
    right: 0;
    bottom: 0;
  }
  .company_info .blocks_elem b {
    font-size: 2.5rem;
  }
  .oborud_text {
    max-width: 420px;
  }
  .map_content {
    right: calc((100% - 960px) / 2);
  }
  .footer_desc {
    grid-template-columns: 2fr 1fr 2fr 1.2fr;
    gap: 3rem;
  }
  .o_vidy_rabot_elem {
    width: 100%;
  }
}
@media screen and (max-width: 991px) {
  .wrapper, .lines {
    width: 720px;
  }
  .header .-desc {
    display: none;
  }
  .main {
    position: relative;
    flex-wrap: wrap;
  }
  .main .-text {
    width: 100%;
  }
  .main .-text .-sub_title {
    max-width: 55%;
  }
  .main .-img {
    position: absolute;
    right: -34px;
    bottom: -47px;
    width: 68%;
  }
  .license {
    flex-wrap: wrap;
  }
  .license_nav {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.5rem;
  }
  .license_nav_wrapper {
    width: 100%;
  }
  .license_nav_elem {
    margin-bottom: 0;
    font-size: 0.85rem;
  }
  .license_content {
    width: 100%;
  }
  .company_img {
    display: none;
  }
  .company_info {
    width: 100%;
  }
  .company_info .h2 {
    max-width: 600px;
  }
  .company_info .-desc {
    width: 100%;
    max-width: none;
  }
  .sertificat {
    grid-template-columns: repeat(4, 1fr);
  }
  .oborud {
    flex-wrap: wrap;
  }
  .oborud_text {
    max-width: none;
  }
  .oborud_text p:last-child {
    margin-bottom: 0;
  }
  .raboty {
    padding: 0;
    gap: 1rem;
  }
  .map_content {
    right: calc((100% - 720px) / 2);
  }
  .footer_desc {
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }
  .footer_desc_elem:nth-child(2) {
    text-align: right;
  }
  .o_vidy_rabot_elem .-head p {
    font-size: 1.2rem;
  }
  .vacancii {
    grid-template-columns: 1fr;
  }
  .kontakty_content {
    gap: 2rem;
  }
  .kontakty_content_elem {
    font-size: 0.9rem;
  }
}
@media screen and (max-width: 767px) {
  html {
    font-size: 12px;
  }
  .wrapper, .lines {
    width: 100%;
    padding: 0 0.8rem;
  }
  section {
    margin-bottom: 9rem;
  }
  .lines div:nth-child(even) {
    display: none;
  }
  .h2 {
    font-size: 1.8rem;
    line-height: 130%;
  }
  .btn {
    padding: 1.1rem 4rem;
    font-size: 1.2rem;
  }
  header {
    position: fixed;
    z-index: 4;
    display: flex;
    justify-content: space-between;
    padding-bottom: 1rem !important;
    background-color: #191E27;
    align-items: center;
    top: 0;
  }
  .header {
    flex-wrap: wrap;
    order: 2;
  }
  .header .-logo {
    display: none;
    width: 90px;
  }
  .header .-info {
    display: none;
  }
  .header .-phones {
    text-align: center;
  }
  .header .-phones a {
    font-size: 1.5rem;
  }
  .header .-phones p {
    font-size: 1rem;
    margin-top: 0.8rem;
  }
  .header_links {
    margin-top: 1rem;
  }
  .header_links_burger {
    display: block;
    width: 40px;
    height: 40px;
    background-image: url("../images/burger.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
  }
  .header_links .header_nav {
    top: 0;
    left: -110%;
    transition: all 0.4s ease-in-out;
    background: rgba(25, 30, 39, 0.98);
    position: fixed;
    z-index: 4;
    width: 100%;
    height: 100%;
    flex-direction: column;
    justify-content: center;
  }
  .header_links .header_nav li {
    margin-bottom: 2.5rem;
    margin-right: 0;
    font-size: 1.2rem;
    text-align: center;
  }
  .header_links .header_nav li ul {
    display: block;
    position: static;
    margin-top: 2.5rem;
  }
  .header_links .header_nav li ul li {
    background: none;
    padding: 0;
    text-align: center;
    margin-bottom: 2.5rem;
  }
  .header_links .header_nav li ul li:hover {
    background-color: transparent;
  }
  .header_links .header_nav li ul li:last-child {
    margin-bottom: 0;
  }
  .header_links .header_nav li ul li a {
    color: #ffffff;
    text-align: center;
  }
  .header_links .header_nav li ul li a:hover {
    background-color: transparent;
    color: #7B25CD;
  }
  .header_links .header_nav.-active {
    left: 0;
  }
  .header_links .-close {
    display: block;
    width: 40px;
    height: 40px;
    position: absolute;
    top: 2rem;
    right: 2rem;
    background-image: url("../images/close.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
  }
  .header_links .-img {
    display: block;
    width: 70%;
  }
  .main {
    padding-top: 14rem;
    margin-top: 0;
    flex-wrap: wrap;
  }
  .main .-img {
    order: 1;
    position: static;
    width: 100%;
  }
  .main .-img .clouds {
    width: 27%;
    left: 42%;
    top: 16%;
  }
  .main .-text {
    margin-top: 1.5rem;
    order: 2;
  }
  .main .-text .h1 {
    line-height: 121%;
  }
  .main .-text .-sub_title {
    max-width: none;
  }
  @keyframes clouds {
    0% {
      left: 42%;
      top: 16%;
    }
    30% {
      opacity: 1;
    }
    100%, 50% {
      top: -70%;
      left: 55%;
      opacity: 0;
    }
  }
  .license_nav {
    grid-template-columns: repeat(4, 1fr);
    width: 410%;
  }
  .license_nav_wrapper {
    overflow-x: scroll;
    padding-bottom: 1rem;
  }
  .license_nav_wrapper::-webkit-scrollbar {
    width: 6px;
    height: 4px;
    background-color: #191E27;
  }
  .license_nav_wrapper::-webkit-scrollbar-thumb {
    background: linear-gradient(270.63deg, #7B25CD 0%, #A74EE5 100%);
    height: 4px;
  }
  .license_nav_wrapper::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
    background-color: #f9f9fd;
    height: 4px;
  }
  .license_nav_elem {
    font-size: 1rem;
    width: 100%;
    max-height: 104px;
    overflow: auto;
  }
  .license_nav_elem::-webkit-scrollbar {
    width: 3px;
    background-color: #191E27;
  }
  .license_nav_elem::-webkit-scrollbar-thumb {
    background: #191E27;
  }
  .license_nav_elem::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
    background-color: #b7b7ba;
  }
  .license_content {
    padding: 1.3rem;
  }
  .license_content .-text ul li {
    font-size: 1rem;
    line-height: 23px;
  }
  .company_info .blocks {
    gap: 2rem;
    width: 100%;
    grid-template-columns: 1fr;
  }
  .company_info .blocks_elem {
    padding: 10px 0;
  }
  .sertificat, .gallery {
    grid-template-columns: 1fr 1fr;
  }
  .fancybox__caption {
    width: 100%;
  }
  .raboty {
    padding: 0 1rem;
    gap: 2rem;
    grid-template-columns: 1fr;
  }
  .map {
    display: flex;
    flex-direction: column;
  }
  .map iframe {
    order: 2;
    height: 300px;
  }
  .map_content {
    padding: 0 1rem;
    background: none;
    transform: none;
    order: 1;
    position: static;
    margin-bottom: 2rem;
    text-align: left;
  }
  .map_content .h2 {
    margin-bottom: 5rem;
  }
  .map_content .-desc {
    margin-bottom: 2.5rem;
  }
  .map_content .-desc p {
    font-size: 1.2rem;
    margin-bottom: 1.2rem;
  }
  .footer_head .nav {
    display: none;
  }
  .footer_desc {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  .footer_desc p {
    font-size: 1rem;
  }
  .footer_desc_elem:nth-child(2) {
    text-align: left;
  }
  .footer_desc .-phones {
    text-align: left;
    font-size: 1.3rem;
  }
  .o_first_screen_padding {
    margin-top: 10rem;
  }
  .o_h1 {
    font-size: 2rem;
    line-height: 138%;
  }
  .o_sub_title {
    width: 100%;
    font-size: 1.2rem;
  }
  .o_vidy_rabot_elem .-head p {
    font-size: 1.1rem;
    line-height: 1.8rem;
  }
  .vacancii_elem .-title {
    font-size: 1.5rem;
  }
  .vacancii_elem .-content_elem {
    grid-template-columns: 1fr;
  }
  .vacancii_elem .-content_elem span {
    width: auto;
    justify-self: start;
    font-size: 1.2rem;
    padding: 1rem 2rem;
  }
  .vacancii_elem .-content_elem p {
    font-size: 1.1rem;
  }
  .kontakty p {
    font-size: 1.2rem;
    line-height: 1.8rem;
  }
  .kontakty_content {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  .kontakty_content_elem {
    text-align: left;
    padding-left: 1rem;
    font-size: 1.2rem;
  }
  .zakupki_wrapper {
    overflow-x: auto;
  }
}
@media screen and (max-width: 575px) {
  .galereya {
    grid-template-columns: 1fr;
  }
}