$white: #fff;
$grad: linear-gradient(270.63deg, #7B25CD 0%, #A74EE5 100%);
$violet: #7B25CD;
$black: #191E27;
// общие стили
html {
  font-family: "Montserrat", "Roboto", "Segoe UI", sans-serif;
  color: $white;
  font-size: 16px;
  background-color: $black;
  position: relative;
}

b, strong {
  font-weight: bold;
}

section {
  margin-bottom: 9rem;
}

.wrapper {
  margin-left: auto;
  margin-right: auto;
  width: 1441px;
}

.lines {
  position: absolute;
  width: 1441px;
  height: 100%;
  display: flex;
  justify-content: space-between;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  z-index: -1;

  div {
    width: 1px;
    height: 100%;
    background-color: rgba(229, 229, 229, 0.2);
  }
}

.btn {
  background: $grad;
  text-align: center;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  border: none;
  color: #fff;
  padding: .9rem 3rem;
  display: inline-block;
  transition: all .3s;

  &::after {
    top: -32px;
    content: '';
    display: block;
    -ms-transform: rotate(25deg);
    transform: rotate(25deg);
    position: absolute;
    width: 20px;
    height: 130px;
    z-index: 2;
    opacity: .55;
    background: #fff;
    -webkit-animation: 2.8s ease-out infinite btn-effect;
    animation: 2.8s ease-out infinite btn-effect;
  }

  &:hover {
    transform: translateY(-3px);
  }

  &_center {
    width: 100%;
    display: flex;
    justify-content: center;
  }
}

.h2 {
  text-align: center;
  font-weight: 400;
  font-size: 2rem;
  line-height: 39px;
}

.mb-5 {
  margin-bottom: 5rem;
}

@keyframes btn-effect {
  0% {
    left: -20%
  }
  100%, 35% {
    left: 110%
  }
}

.text_color {
  background: linear-gradient(270.63deg, #7B25CD 0%, #A74EE5 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;

}

.shadow {
  background: $grad; /* Тень */
  box-shadow: 0 5px 30px rgba(166, 77, 229, 0.6);
}

// ШАПКА
.header {
  display: flex;
  margin-top: 1.5rem;
  justify-content: space-between;
  align-items: center;

  .-logo {
    width: 146px;
  }

  .-info {
    line-height: 20px;
  }

  .-phones {
    text-align: right;

    a {
      font-weight: 700;
      font-size: 20px;
    }

    p {
      cursor: pointer;
      margin-top: 7px;
      font-size: 15px;
      text-decoration: underline;
    }
  }

  &_links {
    margin-top: 2rem;

    &_burger {
      display: none;
    }

    ul {
      display: flex;
      align-items: center;

      li {
        transition: all .3s;
        cursor: pointer;
        position: relative;
        margin-right: 1.5rem;
        font-weight: bold;

        ul {

          position: absolute;
          display: none;
          top: 98%;

          li {
            transition: all .3s;
            background-color: #fff;
            padding: .7rem .9rem;
            width: 100%;
            border-bottom: 1px solid rgba(25, 30, 39, .8);
            line-height: 1.2rem;

            a {
              color: $black;
            }

            &:hover, &:hover a {
              background-color: $violet;
              color: #fff;
            }
          }
        }

        a {
          transition: all .3s;
          color: #fff;
          font-weight: bold;
        }

        &:last-child {
          margin-right: 0;
        }

        &:hover a {
          color: $violet;
        }

        &:hover {
          ul {
            display: block;
          }
        }
      }
    }

    .-close {
      display: none;
    }

    .-img {
      display: none;
    }
  }
}

// 1 ЭКРАН
.main {
  margin-top: 6rem;
  display: flex;
  align-items: center;
  justify-content: space-between;

  .-text {
    width: 48%;
    display: flex;
    flex-direction: column;
    align-items: start;

    .h1 {
      font-weight: 700;
      font-size: 3.375rem;
      line-height: 150%;
      margin-bottom: 2rem;
    }

    .-sub_title {
      font-size: 1.375rem;
      line-height: 150%;
      margin-bottom: 2.5rem;
    }

    .-descr {
      margin-bottom: 4rem;
    }
  }

  .-img {
    width: 48%;
    position: relative;
    perspective: 800px;

    .logo {
      z-index: 1;
      position: relative;
      width: 710px;
      filter: grayscale(100%);
      animation: 2s 1 linear logo forwards;
      animation-delay: 2s;
      perspective-origin: 100% center;
    }

    .clouds {
      position: absolute;
      z-index: 0;
      top: 40px;
      left: 273px;
      animation: 5.5s infinite ease-in-out clouds;
      animation-delay: 5s;
      transform: rotate3d(0, 1, 0, 334deg);
      perspective-origin: 100% center;
    }
  }
}

//АНИМАЦИЯ LOGO
@keyframes logo {
  50% {
    filter: grayscale(0%);
    transform: rotate3d(0, 0, 0, 0);
  }
  100% {
    filter: grayscale(0%);
    transform: rotate3d(0, 1, 0, -20deg);
  }
}

//АНИМАЦИЯ ОБЛАКОВ
@keyframes clouds {
  0% {
    top: 25px;
    left: 261px;
  }

  30% {
    opacity: 1;
  }

  100%,
  50% {
    top: -210px;
    left: 391px;
    opacity: 0;
  }
}


// ЛИЦЕНЗИИ
.license {
  display: flex;
  gap: 6rem;
  justify-content: center;

  &_nav {
    width: 588px;
    flex-shrink: 0;

    &_elem {
      transition: all .3s;
      cursor: pointer;
      padding: 1.5rem 2rem;
      margin-bottom: 1rem;
      background: $black;
      border: 1px solid #FFFFFF;
      line-height: 22px;

      &.-active {
        background: $grad;
        border: transparent;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &_content {
    background: $grad;
    padding: 3rem;
    max-height: 488px;
    overflow-y: auto;

    &::-webkit-scrollbar {
      width: 6px;
      background-color: $grad;
    }

    &::-webkit-scrollbar-thumb {
      background-color: $black;
    }

    &::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
      background-color: #f9f9fd;
    }

    .-text {
      display: none;

      ul {
        li {
          line-height: 24px;
          margin-bottom: 1rem;
        }
      }
    }
  }

  .-active {
    display: block;
  }
}

// О КОМПАНИИ
.company {
  display: flex;

  &_info {
    max-width: 690px;

    .h2 {
      text-align: left;
      line-height: 141.4%;
      margin-bottom: 3rem;
    }

    .-desc {
      max-width: 540px;

      i {
        font-style: italic;
        display: block;
        line-height: 141.4%;
        margin-bottom: 2rem;
      }
    }

    .blocks {
      margin-top: 5rem;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 3rem;
      max-width: 576px;

      &_elem {
        height: 100%;
        text-align: center;
        padding: 20px 0;
        background: $grad;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        b {
          line-height: 59px;
          font-size: 3rem;
        }
      }
    }

  }
}

// СЕРТИФИКАТЫ
.sertificat {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 1.5rem;
}

// ГАЛЕРЕЯ
.gallery {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

// ОБОРУДОВАНИЕ
.oborud {
  display: flex;
  gap: 4rem;

  &_text {
    max-width: 530px;

    p {
      font-size: 1.125rem;
      margin-bottom: 2rem;
      line-height: 141.4%;
    }
  }
}

// НАШИ РАБОТЫ
.raboty {
  padding: 0 2.5rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
}

.fancybox__caption {
  background: #fff;
  color: black;
  padding: 20px 40px;
  width: 50%;
}

// КАРТА
.map {
  position: relative;

  &_content {
    background-color: #191e27;
    padding: 2.5rem 2.5rem 3.75rem;
    position: absolute;
    top: 50%;
    right: calc((100% - 1441px) / 2);
    transform: translateY(-50%);

    .h2 {
      text-align: center;
      margin-bottom: 1.2rem;
    }

    .-desc {
      margin-bottom: 1.5rem;

      p {
        margin-bottom: .75rem;
      }
    }
  }
}

//FOOTER
.footer {
  padding: 2rem 0 3rem;

  &_head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.5rem;

    .-logo {
      width: 160px;
    }

    .nav {
      width: 70%;

      .header_links {
        margin-top: 0;
      }

      ul {
        justify-content: space-between;

        li {
          font-weight: 400;
          font-size: .9rem;

          a {
            font-weight: 400;
            font-size: .9rem;
          }
        }
      }
    }
  }

  &_desc {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 5rem;
    margin-bottom: 2rem;

    p {
      font-size: .9rem;
      margin-bottom: .5rem;
      line-height: 19px;
    }

    .-phones {
      display: flex;
      flex-direction: column;
      text-align: right;
      font-weight: bold;
      font-size: 1.125rem;
      line-height: 145%;
    }
  }

  &_info {
    max-width: 1170px;
    line-height: 145%;
    font-size: .9rem;
  }

}

// ДРУГИЕ СТРАНИЦЫ (начинай класс с буквы "O" (other))
.o_h1 {
  font-size: 2.5rem;
  line-height: 3rem;
}

.o_sub_title {
  width: 80%;
  margin-top: 2rem;
  line-height: 1.8rem;
}

.o_first_screen_padding {
  margin-top: 4rem;
}

// СТРАНИЦА ВИДЫ РАБОТ
.o_vidy_rabot {
  &_elem {
    border: 1px solid #fff;
    margin-bottom: 1rem;
    width: 80%;
    transition: all .3s;

    .-head {
      overflow: hidden;
      position: relative;
      cursor: pointer;
      padding: 1rem 1.5rem;
      display: flex;
      justify-content: space-between;
      align-items: center;

      p {
        font-size: 1.2rem;
        line-height: 29px;
      }

      i {
        margin-left: 2rem;
        transition: all .3s;
        display: block;
        width: 24px;
        height: 24px;
        background-image: url("../images/drop_arrow.png");
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
        transform: rotateZ(180deg);
      }

      &:before {
        transition: all .5s ease-in;
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: -1;
        background: $grad;
        top: 0;
        left: -105%;
      }
    }

    .-content {
      opacity: 0;
      display: none;
      padding: 2rem 1.5rem;
      transition: all .5s linear;

      & p, ul, ol, img {
        margin-bottom: .9rem;
        line-height: 1.7rem;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    &.-active {
      border: 1px solid $violet;

      .-head {
        i {
          transform: rotateZ(0deg);
        }

        &:before {
          left: 0;
        }
      }

      .-content {
        background-color: $black;
      }
    }

    .-visible {
      opacity: 1;
    }

    .-to_block {
      display: block;
    }
  }
}

// ВАКАНСИИ
.vacancii {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 5rem 2.5rem;

  &_elem {
    .-title {
      display: block;
      font-weight: 700;
      font-size: 1.75rem;
      line-height: 2rem;
      margin-bottom: 3rem;
    }

    .-content {
      display: grid;
      grid-template-columns: 1fr;
      gap: 2rem;

      &_elem {
        display: grid;
        grid-template-columns: 1fr 3fr;
        gap: 1.5rem;

        span {
          padding: 1rem 0;
          text-align: center;
          justify-self: stretch;
          align-self: start;
          display: block;
          width: 100%;
          background: $grad;
        }

        p {
          line-height: 22px;
        }
      }
    }
  }
}

// КОНТАКТЫ
.kontakty {
  p {
    margin-bottom: 2.5rem;
  }

  &_content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3.75rem;
    margin-bottom: 3.5rem;

    &_elem {
      text-align: center;
      padding: 1rem 0;
      background: $grad;
      font-weight: bold;
    }
  }
}

// ГАЛЕРЕЯ
.galereya {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;

  &_elem {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;

    a {
      width: 100%;

      img {
        width: 100%;
        object-fit: fill;
      }
    }
  }
}

// ЗАКУПКИ
.zakupki {
  table {
    tr {
      &:first-child {
        td {
          background: $violet;
          border: 1px solid #fff;
        }
      }
      & td:first-child {
        width: 25%;
      }
      & td:nth-child(2) {
        width: 65%;
      }
      & td:last-child {
        text-align: center;
      }
      td {
        padding: 1rem;
        border: 1px solid #fff;
        line-height: 1.45rem;
        vertical-align: middle;
      }
    }

  }
}















