// От 1200px до 1500
@media screen and (max-width: 1500px) {
  html {
    font-size: 14px;
  }
  .wrapper, .lines {
    width: 1170px;
  }

  .main {
    .-img .clouds {
      left: 193px;
    }
  }
  @keyframes clouds {
    0% {
      top: 25px;
      left: 200px;
    }

    30% {
      opacity: 1;
    }

    100%,
    50% {
      top: -210px;
      left: 391px;
      opacity: 0;
    }
  }
  .license {
    gap: 5rem;

    &_content {
      padding: 2.5rem;
    }
  }
  .company_info {
    max-width: 605px;

    .blocks {
      margin-top: 3.5rem;
      gap: 2.3rem;
      width: 500px;
    }

    .-desc {
      max-width: 476px;

      i {
        line-height: 156%;
        margin-bottom: 1rem;
      }
    }
  }
  .map_content {
    right: calc((100% - 1170px) / 2);
  }
}

// От 991 до 1198
@media screen and (max-width: 1199px) {
  .wrapper, .lines {
    width: 960px;
  }
  .main {
    .-text {
      .h1 {
        font-size: 2.8rem;
      }

      .-sub_title {
        font-size: 1.15rem;
      }
    }

    .-img {
      width: 52%;

      .clouds {
        width: 130px;
        left: 180px;
      }
    }
  }
  @keyframes clouds {
    0% {
      top: 25px;
      left: 180px;
    }

    30% {
      opacity: 1;
    }

    100%,
    50% {
      top: -180px;
      left: 341px;
      opacity: 0;
    }
  }
  .license {
    gap: 3rem;

    &_nav {
      width: auto;

      &_wrapper {
        flex-shrink: 0;
        width: 45%;
      }

      &_elem {
        padding: 1.5rem;
      }
    }

    &_content {
      padding: 2rem;
      max-height: 473px;
    }
  }
  .company {
    position: relative;

    &_img {
      position: absolute;
      width: 54%;
      z-index: -1;
      right: 0;
      bottom: 0;
    }

    &_info {
      .blocks_elem {
        b {
          font-size: 2.5rem;
        }
      }
    }
  }
  .oborud_text {
    max-width: 420px;
  }
  .map_content {
    right: calc((100% - 960px) / 2);
  }
  .footer_desc {
    grid-template-columns: 2fr 1fr 2fr 1.2fr;
    gap: 3rem;
  }
  //  ОСТАЛЬНЫЕ СТРАНИЦЫ

  //  ВИДЫ РАБОТ
  .o_vidy_rabot {
    &_elem {
      width: 100%;
    }
  }
}

// От 768 до 990
@media screen and (max-width: 991px) {
  .wrapper, .lines {
    width: 720px;
  }
  .header {
    .-desc {
      display: none;
    }
  }
  .main {
    position: relative;
    flex-wrap: wrap;

    .-text {
      width: 100%;

      .-sub_title {
        max-width: 55%;
      }
    }

    .-img {
      position: absolute;
      right: -34px;
      bottom: -47px;
      width: 68%;
    }
  }
  .license {
    flex-wrap: wrap;

    &_nav {
      &_wrapper {
        width: 100%;
      }

      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 1.5rem;

      &_elem {
        margin-bottom: 0;
        font-size: .85rem;
      }
    }

    &_content {
      width: 100%;
    }
  }
  .company {
    &_img {
      display: none;
    }

    &_info {
      width: 100%;

      .h2 {
        max-width: 600px;
      }

      .-desc {
        width: 100%;
        max-width: none;
      }
    }
  }
  .sertificat {
    grid-template-columns: repeat(4, 1fr);
  }
  .oborud {
    flex-wrap: wrap;

    &_text {
      max-width: none;

      p:last-child {
        margin-bottom: 0;
      }
    }
  }
  .raboty {
    padding: 0;
    gap: 1rem;
  }
  .map_content {
    right: calc((100% - 720px) / 2);
  }
  .footer_desc {
    grid-template-columns: 1fr 1fr;
    gap: 2rem;

    &_elem:nth-child(2) {
      text-align: right;
    }
  }

  //  ОСТАЛЬНЫЕ СТРАНИЦЫ

  //  ВИДЫ РАБОТ
  .o_vidy_rabot {
    &_elem {
      .-head p {
        font-size: 1.2rem;
      }
    }

  }
  // ВАКАНСИИ
  .vacancii {
    grid-template-columns: 1fr;
  }
  // КОНТАКТЫ
  .kontakty_content {
    gap: 2rem;
    &_elem {
      font-size: .9rem;
    }
  }
}

// От 575 до 767
@media screen and (max-width: 767px) {
  html {
    font-size: 12px;
  }
  .wrapper, .lines {
    width: 100%;
    padding: 0 .8rem;
  }
  section {
    margin-bottom: 9rem;
  }
  .lines {
    div:nth-child(even) {
      display: none;
    }
  }
  .h2 {
    font-size: 1.8rem;
    line-height: 130%;
  }
  .btn {
    padding: 1.1rem 4rem;
    font-size: 1.2rem;
  }
  header {
    position: fixed;
    z-index: 4;
    display: flex;
    justify-content: space-between;
    padding-bottom: 1rem !important;
    background-color: $black;
    align-items: center;
    top: 0;
  }
  .header {
    flex-wrap: wrap;
    order: 2;

    .-logo {
      display: none;
      width: 90px;
    }

    .-info {
      display: none;
    }

    .-phones {
      text-align: center;

      a {
        font-size: 1.5rem;
      }

      p {
        font-size: 1rem;
        margin-top: .8rem;
      }
    }

    &_links {
      margin-top: 1rem;

      &_burger {
        display: block;
        width: 40px;
        height: 40px;
        background-image: url("../images/burger.png");
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center center;
      }

      .header_nav {
        top: 0;
        left: -110%;
        transition: all .4s ease-in-out;
        background: rgba(25, 30, 39, .98);
        position: fixed;
        z-index: 4;
        width: 100%;
        height: 100%;
        flex-direction: column;
        justify-content: center;

        li {
          margin-bottom: 2.5rem;
          margin-right: 0;
          font-size: 1.2rem;
          text-align: center;
          ul {
            display: block;
            position: static;
            margin-top: 2.5rem;

            li {
              background: none;
              padding: 0;
              text-align: center;
              margin-bottom: 2.5rem;

              &:hover {
                background-color: transparent;
              }

              &:last-child {
                margin-bottom: 0;
              }

              a {
                color: #ffffff;
                text-align: center;

                &:hover {
                  background-color: transparent;
                  color: $violet;
                }
              }
            }
          }
        }

        &.-active {
          left: 0;
        }
      }

      .-close {
        display: block;
        width: 40px;
        height: 40px;
        position: absolute;
        top: 2rem;
        right: 2rem;
        background-image: url("../images/close.png");
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center center;
      }

      .-img {
        display: block;
        width: 70%;
      }
    }
  }

  .main {
    padding-top: 14rem;
    margin-top: 0;
    flex-wrap: wrap;

    .-img {
      order: 1;
      position: static;
      width: 100%;

      .clouds {
        width: 27%;
        left: 42%;
        top: 16%;
      }
    }

    .-text {
      margin-top: 1.5rem;
      order: 2;

      .h1 {
        line-height: 121%;
      }

      .-sub_title {
        max-width: none;
      }
    }
  }
  @keyframes clouds {
    0% {
      left: 42%;
      top: 16%;
    }

    30% {
      opacity: 1;
    }

    100%,
    50% {
      top: -70%;
      left: 55%;
      opacity: 0;
    }
  }

  .license {
    &_nav {
      grid-template-columns: repeat(4, 1fr);
      width: 410%;

      &_wrapper {
        overflow-x: scroll;
        padding-bottom: 1rem;
        &::-webkit-scrollbar {
          width: 6px;
          height: 4px;
          background-color: $black;
        }

        &::-webkit-scrollbar-thumb {
          background: $grad;
          height: 4px;
        }

        &::-webkit-scrollbar-track {
          -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
          background-color: #f9f9fd;
          height: 4px;
        }
      }

      &_elem {
        font-size: 1rem;
        width: 100%;
        max-height: 104px;
        overflow: auto;
        &::-webkit-scrollbar {
          width: 3px;
          background-color: $black;
        }

        &::-webkit-scrollbar-thumb {
          background: $black;
        }

        &::-webkit-scrollbar-track {
          -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.2);
          background-color: #b7b7ba;
        }
      }
    }

    &_content {
      padding: 1.3rem;

      .-text {
        ul li {
          font-size: 1rem;
          line-height: 23px;
        }
      }
    }
  }

  .company_info {
    .blocks {
      gap: 2rem;
      width: 100%;
      grid-template-columns: 1fr;

      &_elem {
        padding: 10px 0;
      }
    }
  }
  .sertificat, .gallery {
    grid-template-columns: 1fr 1fr;
  }
  .fancybox__caption {
    width: 100%;
  }
  .raboty {
    padding: 0 1rem;
    gap: 2rem;
    grid-template-columns: 1fr;
  }
  .map {
    display: flex;
    flex-direction: column;

    iframe {
      order: 2;
      height: 300px;
    }

    &_content {
      padding: 0 1rem;
      background: none;
      transform: none;
      order: 1;
      position: static;
      margin-bottom: 2rem;
      text-align: left;

      .h2 {
        margin-bottom: 5rem;
      }

      .-desc {
        margin-bottom: 2.5rem;

        p {
          font-size: 1.2rem;
          margin-bottom: 1.2rem;
        }
      }
    }
  }
  .footer {
    &_head {
      .nav {
        display: none;
      }
    }

    &_desc {
      grid-template-columns: 1fr;
      gap: 1.5rem;

      p {
        font-size: 1rem;
      }

      &_elem:nth-child(2) {
        text-align: left;
      }

      .-phones {
        text-align: left;
        font-size: 1.3rem;
      }
    }
  }

  //  ОСТАЛЬНЫЕ СТРАНИЦЫ
  .o_first_screen_padding {
    margin-top: 10rem;
  }
  .o_h1 {
    font-size: 2rem;
    line-height: 138%;
  }
  .o_sub_title{
    width: 100%;
    font-size: 1.2rem;
  }
  //  ВИДЫ РАБОТ
  .o_vidy_rabot {
    &_elem {
      .-head p {
        font-size: 1.1rem;
        line-height: 1.8rem;
      }
    }

  }

  // ВАКАНСИИ
  .vacancii_elem {
    .-title {
      font-size: 1.5rem;
    }
    .-content_elem {
      grid-template-columns: 1fr;
      span {
        width: auto;
        justify-self: start;
        font-size: 1.2rem;
        padding: 1rem 2rem;
      }
      p {
        font-size: 1.1rem;
      }
    }

  }

  // КОНТАКТЫ
  .kontakty{
    p {
      font-size: 1.2rem;
      line-height: 1.8rem;
    }
    &_content {
      grid-template-columns: 1fr;
      gap: 1.5rem;
      &_elem {
        text-align: left;
        padding-left: 1rem;
        font-size: 1.2rem;
      }
    }
  }

  // ЗАКУПКИ
  .zakupki_wrapper {
    overflow-x: auto;
  }
}

// От 0 До 574
@media screen and (max-width: 575px) {
  .galereya {
    grid-template-columns: 1fr;
  }
}